<template>
    <div class="body">
        <div class="navigation">
            <ul :style="ulWidth">

                <li v-for="(item, index) in liData"  :class="item.class" :key="index" @click="activeLink(index,this)">
                    <a href="#">
                        <span class="icon">
                            <i :class="item.icon"></i>
                        </span>
                        <span class="text">{{item.name}}</span>
                    </a>
                </li>
                <div class="indicator" :style="indicatorStyle"></div>
            </ul>
        </div>
    </div>
</template>
<script>
    import _data from "../js/data.js";
    export default {
        name: "TransverMenu",
        data:function() {
            return{
                liData:[],
                indicatorStyle:"transform: translateX(calc(70px * 0))",
                ulWidth:"width:350px"
            }
        },
        props: {
        },
        created() {
            this.$nextTick(()=>{
                this.liData = _data._data.liData;
                this.ulWidth = `width:${70*this.liData.length}px`
            })
            this.$router.push({ path: '/'})
        },
        methods:{
            activeLink:function (index) {
                const list = document.querySelectorAll(".navigation li");
                list.forEach((item,index)=>{
                    item.classList.remove("active")
                })
                this.indicatorStyle  = `transform: translateX(calc(70px * ${index}))`
                list[index].classList.add("active");
                this.$router.push({ path: this.liData[index].router || "nothing"})
            }
        }
    }
</script>
<style scoped>
    *{
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        font-family: 'Poppins',sans-serif;
    }
    :root{
        --clr:#222327;
    }
    .body{
        display: flex;
        justify-content: center;
        align-items: center;
        min-height: 13vh;
        background: #222327;
    }
    .navigation{
        position: relative;
        width: 95%;
        height: 70px;
        background: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 30px;
    }
    .navigation ul{
        display: flex;
    }
    .navigation ul li{
        position: relative;
        cursor: pointer;
        list-style: none;
        width: 70px;
        height: 70px;
        z-index: 1;
    }
    .navigation ul li a{
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        width: 100%;
        text-align: center;
        font-weight: 500;
    }
    .navigation ul li a .icon{
        position: relative;
        display: block;
        line-height: 75px;
        font-size: 1.5em;
        text-align: center;
        transition: 0.5s;
        color: #222327;
    }
    .navigation ul li.active a .icon{
        transform: translateY(-35px);
    }
    .navigation ul li a .text{
        position: absolute;
        color: #222327;
        font-weight: 400;
        font-size: 0.75em;
        letter-spacing: 0.05em;
        transition: 0.5s;
        opacity: 0;
        transform: translateY(20px);
    }
    .navigation ul li.active a .text{
        opacity: 1;
        transform: translateY(10px);
    }

    .indicator{
        position: absolute;
        top:-45%;
        width: 70px;
        height: 70px;
        background: #29fd53;
        border-radius: 50%;
        border: 6px solid #222327;
        transition: 0.5s;
    }
    .indicator::before{
        content: "";
        position: absolute;
        top: 44%;
        left: -22px;
        width: 18px;
        height: 20px;
        background: transparent;
        border-top-right-radius: 20px;
        box-shadow: 0px -10px 0 0 #222327;
    }
    .indicator::after{
        content: "";
        position: absolute;
        top: 44%;
        right: -22px;
        width: 18px;
        height: 20px;
        background: transparent;
        border-top-left-radius: 20px;
        box-shadow: 0px -10px 0 0 #222327;
    }








</style>